import {v4 as uuidv4} from 'uuid';
import {createApp} from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

createApp(App).mixin({
    data() {
        return {
            componentId: uuidv4()
        }
    },
}).use(store).use(router).mount('#app')
