import {createStore} from 'vuex'

export default createStore({
    state: {
        peer: null
    },
    getters: {
        PEER: state => {
            return state.peer
        }
    },
    mutations: {
        SET_PEER: (state, payload) => {
            state.peer = payload
        }
    },
    actions: {
        SET_PEER: (context, payload) => {
            context.commit('SET_PEER', payload);
        }
    },
    modules: {}
})
